.section-about {
  padding: 40px 0 0 0;
  background-color: var(--bg-color3);
}

.section-about__bg-milk {
  @media screen and (max-width: 767px) {
    background-repeat: no-repeat;
    background-size: contain;
    background-position: 0% 78%;
    background-image: url(../images/milk-320.png);
    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background-image: url(../images/milk-640.png);
    }
  }

  @include desktop {
    background-image: url(../images/milk-1278.png);
    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background-image: url(../images/milk-2556.png);
    }
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
    width: auto;
    max-width: 1280px;
  }
}

.section-about__label {
  font-family: 'Titan One';
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.17;
  text-align: center;
  text-transform: uppercase;
  margin-left: auto;
  margin-right: auto;
  color: var(--title-font-color2);
}

.section-about__title {
  max-width: 200px;
  height: 76px;
  margin-top: 15px;
  margin-left: auto;
  margin-right: auto;
  font-family: 'Titan One';
  font-style: normal;
  font-weight: 400;
  font-size: 34px;
  line-height: 1.15;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;

  color: var(--title-font-color1);
}

.section-about__img {
  margin-top: 15px;
}

.section-about__text {
  width: 220px;
  font-family: 'DM Sans';
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.62;
}

.section-about__text--first {
  color: var(--paragraph-color1);
  margin: 40px 0;
}

.section-about__text--second {
  color: var(--paragraph-color2);
}

.about-btn-read-more {
  margin-top: 20px;
  padding: 0 37px;
  background-color: var(--btn-color2);
  border-radius: 22px;
  width: 176px;
  height: 44px;
  font-family: 'DM Sans';
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.28;
  display: flex;
  align-items: center;
  text-align: center;

  color: var(--accent-font-color);

  transition: background-color var(--timing-duration) var(--timing-function);

  &:hover,
  &:focus {
    background-color: var(--btn-hover-color2);
  }
}

.about-btn-read-more__icon {
  margin-left: 13px;
  //  border: 2px solid var(--accent-font-color);
}
